<template>
  <div>
    <core-progress-modal :show="loading" />
    <v-tabs
      v-if="!loading"
      color="primary"
      dark
      icons-and-text
    >
      <v-tabs-slider color="white" />

      <v-tab href="#online">
        SPC Online
        <v-icon>mdi-cloud-sync</v-icon>
      </v-tab>

      <v-tab href="#arquivo">
        Remessa Arquivo
        <v-icon>mdi-file-account</v-icon>
      </v-tab>

      <v-tab-item
        value="online"
        lazy
      >
        <spc-remessa-online
          :redes="redes"
          :clientes-em-acordo="clientesEmAcordo"
          :servico-spc-list="servicoSPCList"
          :remessa-id="remessaId"
        />
      </v-tab-item>
      <v-tab-item
        value="arquivo"
        lazy
      >
        <spc-remessa-arquivo
          :redes="redes"
          :clientes-em-acordo="clientesEmAcordo"
          :servico-spc-list="servicoSPCList"
        />
      </v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
import { TiposBusiness } from '../../business'
import { TODAS_ID } from '../../constants'

const SpcRemessaArquivo = () => import('./SpcRemessaArquivo.vue')
const SpcRemessaOnline = () => import('./SpcRemessaOnline.vue')

export default {
  metaInfo: {
    titleTemplate: '%s - SPC'
  },
  components: {
    SpcRemessaArquivo,
    SpcRemessaOnline
  },
  data () {
    return {
      clientesEmAcordo: [
        { id: TODAS_ID, descricao: 'Todos' },
        { id: 1, descricao: 'Apenas os em Acordo' },
        { id: 2, descricao: 'Exceto os em Acordo' }
      ],
      servicoSPCList: [],
      redes: [],
      loading: false,
      remessaId: '0'
    }
  },
  beforeMount () {
    this.loading = true
    this.redes.push({ id: TODAS_ID, descricao: 'Todas Redes' })
    TiposBusiness.getAllRedes()
      .then(response => {
        this.redes = this.redes.concat(response.data)
      })
      .finally(() => {
        TiposBusiness.getAllServicoSPC()
          .then(response => { this.servicoSPCList = response.data })
          .catch(() => { this.$root.showErro('Não foi possível buscar as informações dos motores de créditos') })
          .finally(() => {
            this.loading = false
          })
      })
    if (this.$route.params.remessaId) {
      this.remessaId = this.$route.params.remessaId
    }
  }
}
</script>
